:root{
  --bg-color: #142129;
  --primary-text-color: #F7DAD9;
  --secondary-text-color: #0B7A75;
  --text-highlight-color: #957FEF;
}

html {
  scroll-behavior: smooth;
  max-width:100vw;
  overflow-x: hidden;
  scrollbar-width: none;
}

body{
  color: #F7DAD9;
  color: var(--primary-text-color);
  -ms-overflow-style: none;
}

body::-webkit-scrollbar{
  display: none;
}

h1{
  color: #F7DAD9;
  color: var(--primary-text-color);
  font-size: 15vh;
  line-height: 20vh;
  text-align: right;
}

h2{
  color: #957FEF;
  color: var(--text-highlight-color);
}

h3{
  color: #c29789;
  font-size: 10vh;
  line-height: 1;
  text-decoration: none;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.split{
  width: 100vw;
  max-width:100vw;
  position: relative;
}

.split div{
  width:100%;
  position:absolute;
}

.slide-in-out{
  -webkit-animation-name: slidein, slideout;
  animation-name: slidein, slideout;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-delay: 0s, 6s;
  animation-delay: 0s, 6s;
  overflow:hidden;
}

.slide-in-out2{
  margin-left: 100%;
  width: 0;
  /* opacity: 0%; */
  -webkit-animation-name: slidein, slideout;
  animation-name: slidein, slideout;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-delay: 6.5s, 11.5s;
  animation-delay: 6.5s, 11.5s;
  overflow:hidden;
}

@-webkit-keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
    /* opacity: 0%; */
  }

  to {
    margin-left: 0%;
    width: 100%;
    /* opacity: 100%; */
  }
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
    /* opacity: 0%; */
  }

  to {
    margin-left: 0%;
    width: 100%;
    /* opacity: 100%; */
  }
}

@-webkit-keyframes slideout {
  from {
    margin-left: 0%;
    width: 100%;
    /* opacity: 100%; */
  }

  to {
    margin-left: 100%;
    width: 0%;
    /* opacity: 0%; */
  }
}

@keyframes slideout {
  from {
    margin-left: 0%;
    width: 100%;
    /* opacity: 100%; */
  }

  to {
    margin-left: 100%;
    width: 0%;
    /* opacity: 0%; */
  }
}

.container{
  width: 100vw;
  max-width: 100vw;
  background-color: #142129;
  background-color: var(--bg-color);
}

.highlight{
  color: #957FEF;
  color: var(--text-highlight-color);
}

.intro{
  display: -webkit-flex;
  display: flex;
}

.learn-more{
  position: absolute;
  z-index: 1;
  border: 5px #0B7A75 solid;
  border: 5px var(--secondary-text-color) solid;
  border-radius: 4px;
  transition: all .3s ease-in-out;
}

.learn-more:hover{
  border: 5px #F7DAD9 solid;
  border: 5px var(--primary-text-color) solid;
  color: #0B7A75;
  color: var(--secondary-text-color)
}

.wave1{
  margin-top: 100vh;
  background-color: #CAD2C5;
}

#learnMore{
  height: 100vh;
}

.about-me{
  background-color: #CAD2C5;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow: auto;
  /* padding: 0 5rem 0 5rem; */
}

.about-desc{
  color: #665d65;
}

.button{
  cursor: pointer;
  margin: auto;
  padding: 10px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.5rem;
}

.resume{
  border: 6px solid #0B7A75;
  border: 6px solid var(--secondary-text-color);
  border-radius: 4px;
  color: #957FEF;
  color: var(--text-highlight-color);
  margin-top: 50px;
  margin-right: 10%;
  transition: all .3s ease-in-out;
}

.resume:hover{
  border: 6px solid #957FEF;
  border: 6px solid var(--text-highlight-color);
  color: #0B7A75;
  color: var(--secondary-text-color);
}

.text{
  max-width: 75ch;
  font-size: 1.5rem;
  line-height: 2;
  font-weight: bold;
}

.wave2{
  background-color: #053836;
}

.projects{
  background-color: #053836;
}

.project-image{
  box-shadow: 5px 5px 20px black;
  margin: 2rem 0 2rem 0;
}

.project-links{
  text-align: center;
  margin: 2rem 0 2rem 0;
}

.live{
  margin-right: 2rem;
  border: 5px #957FEF solid;
  border: 5px var(--text-highlight-color) solid;
  border-radius: 4px;
  transition: all .3s ease-in-out;
}

.live:hover{
  border-color: #F7DAD9;
  border-color: var(--primary-text-color);
  color: #957FEF;
  color: var(--text-highlight-color);
}

.source-code{
  border: 5px #957FEF solid;
  border: 5px var(--text-highlight-color) solid;
  border-radius: 4px;
  transition: all .3s ease-in-out;
}

.source-code:hover{
  border-color: #F7DAD9;
  border-color: var(--primary-text-color);
  color: #957FEF;
  color: var(--text-highlight-color);
}

.project{
  background-color: #053836;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow: auto;
  box-shadow:  0 4px 5px -2px black;
  margin-bottom: 2rem;
  overflow-x: hidden;
}

.last-project{
  background-color: #053836;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow: auto;
  overflow-x: hidden;
}

.wave3{
  background-color: #142129;
  background-color: var(--bg-color);
}

.contact{
  padding-bottom: 100px;
  background-color: #142129;
  background-color: var(--bg-color);
}

.contact-info{
  text-align: center;
}

.contact-info p {
  margin-top: 2rem;
}
.particle{
  z-index: 0;
  pointer-events: none;
  position: absolute !important;
}

.toggle{
  position: fixed;
  z-index: 30;
  background: rgba(29, 149, 136, .8);
  bottom: 0;
  right: 0;
  margin: 0 .5rem .5rem 0;
  padding: 1rem;
  border-radius: 10px;
}

.toggle-checkbox{
  border: 0px;
  right:0;
}

.toggle-checkbox:checked {
  left: 0;
  border-color: #000;
}

.toggle-checkbox:checked + .toggle-label {
  background-color: #D1D5DB;
}
